/**
 * define basic styles
 * bqliu | 07/22/2017
 */
html {
  height: 100%;
  box-sizing: border-box;
}
html body {
  position: relative;
  height: 100%;
}
html *,
html *:before,
html *:after {
  box-sizing: inherit;
}
ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}
img {
  border: none;
  display: inline-block;
}
.optimize-scroll {
  -webkit-overflow-scrolling: touch;
}
.xicon {
  vertical-align: middle;
  display: inline-block;
  background-size: 100%;
  background-position: center center;
  background-repeat: no-repeat;
}
.xicon-up {
  width: 0.48rem;
  height: 0.5rem;
  background-image: url(../assets/xicon-up.png);
}
.xicon-up--liked {
  width: 0.48rem;
  height: 0.5rem;
  background-image: url(../assets/xicon-up-liked.png);
}
.xicon-comment {
  width: 0.5rem;
  height: 0.5rem;
  background-image: url(../assets/xicon-comment.png);
}
.xicon-main {
  width: 0.34rem;
  height: 0.53rem;
  background-image: url(../assets/xicon-main.png);
}
.xicon-add {
  width: 0.61rem;
  height: 0.61rem;
  background-image: url(../assets/xicon-add.png);
}
.xicon-mine {
  width: 0.4rem;
  height: 0.52rem;
  background-image: url(../assets/xicon-mine.png);
}



